    staticClass: "koubei-desc"
  }, [_vm._v("\n\t\t\t\t\t\t\t结识蜜芽是在一位友人的朋友圈，那时候我宝宝才满月，吃的是明一奶粉，那奶粉对于我来说还挺贵的，在我们这边超市368一罐，对于一个没工作的宝妈来说确实有点承受不了，不过我都很想得开，因为每个麻麻都想给自己孩子最好的，我也不例外，所以我想只要宝宝喝了好，自己节约一点就好，那奶粉很甜，宝贝吃了上火很严重，那会吃明一的时候宝贝眼睛全眼屎，每天都要给他擦很多次，有时候眼睛都擦红了，但还是没用，有时候眼屎多的眼睛都睁不开，在这要说明一下，并不是我偏袒牛栏吧，因为我家喝明一确实很上火，不过因人而异吧。一开始我看很多宝宝都吃的牛栏，所以抱着试一试的心态在蜜芽买了一1罐来试一试，奶粉很清淡，味道微甜，我尝了一下，于是迫不及待的给宝宝直接换成牛栏了，原谅我是一个90后的新手麻麻，对于这些常识根本一窍不通换，以致最后害宝宝拉了几天肚子。而且一开始给宝宝在蜜芽上买奶粉其实是有顾虑的，因为奶粉不同于玩具和衣服，毕竟是要进肚皮里的，而且不是只吃一两次，因为我家是全奶粉，所以我有这方面的顾虑也很正常，最后我到实体店买了2罐和蜜芽上买到的牛栏做了细致的比较，最后的结果还比较满意，除了生产日期和保质期不同，其他基本一样，味道也一样，实体店225一罐，比蜜芽上贵太多，我肯定会选择经济实惠的来买了，所以我家直到现在吃的都是蜜芽上买的牛栏！包装也蛮好的，从一开始到现在从来没有出现过爆罐这种情况。下面是我这个6.1淘的牛栏一段和二段奶粉，还有两套小背带套装，6.1晒单，顺便晒晒我的小牛牛，拍摄得不好，请大家凑合看吧，希望超级免单大奖能够砸中我。\n\t\t\t\t\t\t")]), _vm._v(" "), _c('div', {
